.app-hide {
    background: rgb(0, 0, 0);
    z-index: -1;
    opacity: 0.8;
}
.error-window {
    display: none;
}
.window-active {
    display: block;
    position: absolute;
    content: '';
    top: 30%;
    left: 30%;
    right: 30%;
    width: 40%;
    z-index: 20;
    background: white;
}
.window-header {
    height: 100px;
    background: rgba(255, 61, 0, 0.38);
}
.window-sign {
    width: 32px;
    height: 32px;
    position: absolute;
    content: '';
    left: 7%;
    top: 7%;
    background-image: url('/src/images/atten.png');
    background-size: cover;
    transform: rotate(180deg);
}
.window-text {
    width: 93%;
    padding: 20px 20px 20px 20px;
    border-bottom: 1px dashed #928F94;
}
.window-text-first {
    font-size: 1.5rem;
    font-weight: 500;
}
.window-text-second { 
    font-size: 1.125rem;
    font-weight: 400;
    margin-top: 20px;
}
.window-button-container {
    height: 90px;
}
.window-button {
    background: transparent;
    border: 1px solid #292929;
    border-radius: 5px;
    padding: 12px 35px;
    margin-left: 37%;
    margin-top: 3%;
    font-size: 1.5rem;
    font-weight: 700;
}